html {
    height: 100%;
}

body {
    font-family: "Muli", Arial, sans-serif;
    font-weight: 800;
    text-align: left;
    min-height: 100%;
    font-size: 12px;
    color: black;
    z-index: 1;
}

.stopwatch {
    display: flex;
    position: relative;
    left: 0;
    text-align: center;
}

.stopwatch__display {

    padding-left: 20px;
    margin-top: 10px;
    font-family: 'Roboto Mono', monospace;
    font-size: 30px;
    color: #d5c44e;
}

.butt {
    font-weight: 800;

}
